import {Alert, ScrollView, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import {Icon} from '@ui-kitten/components';
import TextCM from '../components/Text';
import {CommonUtils, DateUtils} from '../../utils';
import {useEffect, useState} from 'react';
import NetInfo, {NetInfoState} from '@react-native-community/netinfo';
import {useAccountInfoServices} from '../../hooks/apiHooks/useAccountInfoServices';
import ModalFullScreen from '../components/ModalFullScreen';
import CalenderDetail from './components/calender-detail';
import Geolocation from '@react-native-community/geolocation';
import HeaderCM from '../components/Header/HeaderCM';
import {Color, Font} from '../../constants';
import {NetworkInfo} from 'react-native-network-info';
import {useAppSelector} from '../../hooks/useRedux';
import {ICheckInOut} from './helper';
import Toast from 'react-native-toast-message';

type Props = {
  type?: 'wfh' | 'onsite' | 'checkinout';
  onClose?: () => void;
};

const CheckInOut = ({type, onClose}: Props) => {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [networkState, setNetworkState] = useState<NetInfoState>();
  const [showModal, setShowModal] = useState<'wfh' | 'calender' | 'onsite'>();
  const [stateInOut, setStateInOut] = useState<{
    dataIn?: string;
    dataOut?: string;
    locationIn?: string;
    locationOut?: string;
  }>();
  const [location, setLocation] = useState<any>(null);
  const [dataLocal, setDataLocal] = useState<any>(null);
  const [ip, setIP] = useState<{
    ssid: string | null;
    ipGateway: string | null;
  }>();
  const [isLoading, setIsLoading] = useState(false);
  const {devicesInfo} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);
  const [dataCheckInOut, setDataCheckInOut] = useState<ICheckInOut>();
  const AccountServices = useAccountInfoServices();

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentDate(new Date());
    }, 1000); // Update every second

    return () => clearInterval(timer); // Clean up on unmount
  }, []);

  useEffect(() => {
    // Subscribe
    const unsubscribe = NetInfo.addEventListener(async state => {
      console.log('Connection type', state.type);
      console.log('Is connected?', state.isConnected);
      if (state.isConnected) {
        // Get SSID
        const SSID = await NetworkInfo.getSSID();
        // Get Default Gateway IP
        const defaultGateway = await NetworkInfo.getGatewayIPAddress();
        console.log('Default Gateway IP:', defaultGateway);
        setIP({
          ssid: SSID,
          ipGateway: defaultGateway,
        });
      } else {
        Toast.show({
          type: 'error',
          text1: 'Thông báo',
          text2: 'Không có kết nối mạng',
        });
      }
    });

    // Unsubscribe
    return () => unsubscribe();
  }, []);

  useEffect(() => {
    try {
      Geolocation.getCurrentPosition(
        async position => {
          if (!position?.coords) return;
          const dataLocal = {
            timestamp: position.timestamp,
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          };
          setDataLocal(dataLocal);
        },
        error => {
          console.log('Error getting location:', error);
        },
      );
    } catch (error) {}
  }, []);

  const getLocation = () => {
    try {
      Geolocation.getCurrentPosition(
        async position => {
          if (!position?.coords) return;
          const dataLocal = {
            timestamp: position.timestamp,
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          };
          setDataLocal(dataLocal);
          const {latitude, longitude} = position.coords;
          const data = {lon: longitude, lat: latitude};
          const dataLocation = await AccountServices.fetchLocation(data);
          setLocation(dataLocation);
          handleCheckInOut(dataLocation);
        },
        error => {
          console.log('Error getting location:', error);
        },
      );
    } catch (error) {}
  };

  const handleCheckInOut = (location?: any) => {
    if (!stateInOut?.dataIn) {
      // Handle check-in logic
      setStateInOut({
        ...stateInOut,
        dataIn: currentDate.toISOString().toString(),
        locationIn: location ? JSON.stringify(location) : undefined,
      });
    } else {
      // Handle check-out logic
      setStateInOut({
        ...stateInOut,
        dataOut: currentDate.toISOString().toString(),
        locationOut: location ? JSON.stringify(location) : undefined,
      });
    }
  };
  const getNameCheckInOut = () => {
    if (!stateInOut?.dataIn) {
      return 'Check in';
    } else {
      return 'Check out';
    }
  };

  const getHeaderCheckInOut = () => {
    if (type === 'wfh' || type === 'onsite') {
      return (
        <HeaderCM
          style={{
            backgroundColor: '#fff',
          }}
          contentStyle={{
            backgroundColor: '#fff',
            borderBottomWidth: 1,
            borderBottomColor: Color.borderColor,
          }}
          titleStyle={{
            color: '#000810',
          }}
          fillIconBackLeft="#000810"
          title={type === 'wfh' ? 'WFH' : 'OnSite'}
          onPressIconLeft={onClose}
        />
      );
    }
  };

  const getOnsite = () => {
    if (type === 'onsite') {
      return (
        <ScrollView
          showsVerticalScrollIndicator={false}
          showsHorizontalScrollIndicator={false}
          style={{
            flex: 1,
          }}>
          <TextCM
            style={{
              fontStyle: 'italic',
              fontSize: 12,
              fontFamily: Font.InterMedium500,
            }}>
            * chức năng này sẽ lấy thông tin vị trí của bạn
          </TextCM>

          {stateInOut?.locationIn && (
            <View>
              <TextCM>Checkin</TextCM>
              <TextCM>{JSON.stringify(stateInOut?.locationIn)}</TextCM>
            </View>
          )}

          {stateInOut?.locationOut && (
            <View>
              <TextCM>Checkout</TextCM>
              <TextCM>{JSON.stringify(stateInOut?.locationOut)}</TextCM>
            </View>
          )}
        </ScrollView>
      );
    }
  };

  const getWFH = () => {
    if (type === 'wfh') {
      return (
        <ScrollView
          showsVerticalScrollIndicator={false}
          showsHorizontalScrollIndicator={false}
          style={{
            flex: 1,
          }}>
          <TextCM
            style={{
              fontSize: 16,
              fontFamily: Font.InterSemiBold600,
              textDecorationLine: 'underline',
            }}>
            Cán bộ quản lý phê duyệt
          </TextCM>
        </ScrollView>
      );
    }
  };

  const getFuncCheckInOut = () => {
    handleSubmit();
    // switch (type) {
    //   case 'wfh':
    //     return handleCheckInOut();
    //   case 'onsite':
    //     return getLocation();
    //   case 'checkinout':
    //     return handleCheckInOut();
    //   default:
    //     return handleCheckInOut();
    // }
  };

  const handleSubmit = async () => {
    const data = {
      ...dataLocal,
      ssid: ip?.ssid || 'not found',
      ipGateway: ip?.ipGateway,
      deviceId: devicesInfo?.deviceId,
      deviceName: devicesInfo?.deviceName,
      signature: '', // HMAC-SHA256
    };
    const signature = CommonUtils.getSignature(data, dataAccount?.id);
    data.signature = signature;
    Alert.alert('noti', JSON.stringify(data));
    console.log(data);
  };

  return (
    <>
      {getHeaderCheckInOut()}
      <View
        style={[
          styles.ctnContainer,
          (type === 'wfh' || type === 'onsite') && {backgroundColor: '#fff'},
        ]}>
        <View style={styles.ctnWifi}>
          <View style={{rowGap: 6}}>
            <View style={styles.ctnItemWifi}>
              <Icon
                fill={'#4e1d72'}
                name="wifi-outline"
                width={24}
                height={24}
              />
              <TextCM style={styles.txtWifi}>{ip?.ssid || '---'}</TextCM>
            </View>
            <View style={styles.ctnItemWifi}>
              <Icon
                fill={'#4e1d72'}
                name="calendar-outline"
                width={24}
                height={24}
              />
              <TextCM  
                style={{
                  color: '#00204DE5',
                  fontSize: 14,
                  fontFamily: Font.InterRegular400,
                }}>
                {DateUtils.formatDateVietnamese(currentDate)}
              </TextCM>
            </View>
          </View>
          <View style={styles.ctnTime}>
            <TextCM style={styles.txtTime}>
              {DateUtils.getCurrentTime(currentDate)}
            </TextCM>
          </View>
        </View>

        <View style={styles.ctnBtnCheck}>
          <TouchableOpacity
            style={{
              ...styles.ctnBtn,
              backgroundColor: '#39ac6c',
            }}>
            <Icon name="clock-outline" width={20} height={20} fill={'#fff'} />
            <TextCM style={styles.txtBtn}>Đi công tác</TextCM>
          </TouchableOpacity>

          <TouchableOpacity
            onPress={getFuncCheckInOut}
            style={{
              ...styles.ctnBtn,
              backgroundColor: '#943cdd',
            }}>
            <Icon name="clock-outline" width={20} height={20} fill={'#fff'} />
            <TextCM style={styles.txtBtn}>{getNameCheckInOut()}</TextCM>
          </TouchableOpacity>
        </View>

        <View
          style={[
            styles.ctnWifi,
            (type === 'onsite' || type === 'wfh') && {marginBottom: 16},
          ]}>
          <View style={{rowGap: 6, flex: 1}}>
            <View style={styles.itemDate}>
              <Icon
                name="alert-triangle-outline"
                width={24}
                height={24}
                fill={'#febd02'}
              />
              <TextCM style={styles.txtDate}>
                {stateInOut?.dataIn
                  ? `Check in thành công ${DateUtils.getCurrentTime(
                      stateInOut?.dataIn,
                    )}`
                  : 'Bạn chưa chấm công vào'}
              </TextCM>
            </View>
            <View style={styles.itemDate}>
              <Icon
                name="alert-triangle-outline"
                width={24}
                height={24}
                fill={'#febd02'}
              />
              <TextCM style={styles.txtDate}>
                {stateInOut?.dataOut
                  ? `Check out thành công ${DateUtils.getCurrentTime(
                      stateInOut?.dataOut,
                    )}`
                  : 'Bạn chưa chấm công ra'}
              </TextCM>
            </View>
          </View>

          <TouchableOpacity
            onPress={() => setShowModal('calender')}
            style={styles.ctnCalendar}>
            <Icon
              name="calendar-outline"
              width={24}
              height={24}
              fill={'#943cdd'}
            />
          </TouchableOpacity>
        </View>

        {type === 'checkinout' && (
          <View style={styles.ctnBtnCheck}>
            <TouchableOpacity
              onPress={() => setShowModal('wfh')}
              style={{
                ...styles.ctnBtn,
                backgroundColor: '#366ae2',
              }}>
              <Icon name="home-outline" width={20} height={20} fill={'#fff'} />
              <TextCM style={styles.txtBtn}>WFH</TextCM>
            </TouchableOpacity>

            <TouchableOpacity
              onPress={() => setShowModal('onsite')}
              style={{
                ...styles.ctnBtn,
                backgroundColor: '#fd7a1b',
              }}>
              <Icon name="clock-outline" width={20} height={20} fill={'#fff'} />
              <TextCM style={styles.txtBtn}>OnSite</TextCM>
            </TouchableOpacity>
          </View>
        )}

        {getOnsite()}
        {getWFH()}
      </View>
      <ModalFullScreen
        isVisible={showModal === 'calender'}
        children={<CalenderDetail onClose={() => setShowModal(undefined)} />}
      />

      <ModalFullScreen
        isVisible={showModal === 'wfh'}
        children={
          <CheckInOut type="wfh" onClose={() => setShowModal(undefined)} />
        }
      />

      <ModalFullScreen
        isVisible={showModal === 'onsite'}
        children={
          <CheckInOut type="onsite" onClose={() => setShowModal(undefined)} />
        }
      />
    </>
  );
};

export default CheckInOut;
